@tailwind base;
@tailwind components;
@tailwind utilities;

*{
    margin:0;
    padding: 0;
    font-family: 'Poppins', 'Open Sans';
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
  }

  :root {
    --scrollbar-width: 4px;
    --scrollbar-height: 6px;
  }
  
  /* Style the scrollbar */
  /* For WebKit browsers */
  ::-webkit-scrollbar {
    width: var(--scrollbar-width);
    height: var(--scrollbar-height);
  }
  
  /* For WebKit browsers - thumb */
  ::-webkit-scrollbar-thumb {
    background:#FDBA74; /* adjust color as needed */
    border-radius: 5px;
  }
  
  /* For WebKit browsers - track */
  ::-webkit-scrollbar-track {
    background: #cbccd0;/* adjust color as needed */
  }
